.app__aboutus {
  position: relative;
}

.app__aboutus-overlay {
  position: absolute;
  inset: 0;
}

.app__aboutus-overlay img {
  width: 391px;
  height: 415px;
  z-index: 0;
}

.app__aboutus-content {
  width: 100%;
  z-index: 2;
}

.app__aboutus-content_about {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  text-align: right;
}

.app__aboutus-content-knife {
  margin: 2rem 4rem;
}

.app__aboutus-content-knife img {
  height: 900px;
}

.app__aboutus-content_history {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.app__aboutus-content_about p,
.app__aboutus-content_history p {
  margin: 2rem 0;
  color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
  .app__aboutus-content-knife img {
    height: 1100px;
  }
}
@media screen and (max-width: 1150px) {
  .app__aboutus-content {
    flex-direction: column;
  }
  .app__aboutus-content-knife img {
    height: 800px;
  }
  .app__aboutus-content-knife {
    margin: 4rem 0;
  }
}
@media screen and (max-width: 650px) {
  .app__aboutus-content-knife img {
    height: 600px;
  }

  .app__aboutus-overlay img {
    height: 320px;
    width: 80%;
  }
}
